.side-wrapper {
  padding: 10px 0 0 0;
  //border-right: 1px solid #fff;
  min-height: calc(100vh - 65px);
  float: left;
  * {
    color: #fff;
    font-size: 12px;
  }
  .ant-menu, .ant-menu-dark, .ant-menu-dark .ant-menu-inline.ant-menu-sub {
    background: transparent;
    .ant-menu-inline.ant-menu-sub {
      box-shadow: 0 0 0 0;
    }
    .ant-menu-item {
      a {
        color: #fff;
      }
    }
  }
}