.login-bg {
  .bg-mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgb(0, 0, 0);
    opacity: .5;
  }
  .content-wrapper {
    width: 800px;
    height: 200px;
    position: fixed;
    top: 50%;
    left: 150px;
    margin-top: -100px;
    * {
      color: #fff;
    }
    .content {
      p {
        margin: 0;
        font-size: 16px;
        letter-spacing: 0.45px;
        line-height: 40px;
        &.title {
          margin-bottom: 20px;
          font-weight: 700;
          font-size: 40px;
          line-height: 1.5;
        }
      }
    }
  }
  .login-form-wrapper {
    width: 400px;
    padding: 40px;
    border-radius: 6px;
    height: 340px;
    position: fixed;
    top: 50%;
    right: 120px;
    margin-top: -170px;
    background: #fff;
    box-sizing: border-box;
    .login-form {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      .login-title {
        margin: 0 0 40px;
        color: rgba(0, 0, 0, .8);
        font-size: 28px;
        font-weight: 500;
      }
      .login-btn {
        margin-top: 30px;
        width: 100%;
        height: 40px;
        background: #00a0fe;
        border-radius: 6px;
        line-height: 40px;
        color: #fff;
        text-align: center;
        font-size: 16px;
      }
    }
  }
}