.edit-menu {
  overflow: hidden;

  .menu-right {
    width: 50%;
    border: 1px solid #ddd;
    height: 430px;
    background: #f4f5f9;
    padding: 10px;

    .menu-title {
      margin-bottom: 20px;
      overflow: hidden;
      line-height: 30px;
      font-size: 14px;
      border-bottom: 1px solid #ddd;
    }
  }

  .menu-right-btm {
    width: 50%;
    margin-top: 30px;
  }

  .menu-left {
    width: 40%;

    .menu-wrapper {
      width: 300px;
      height: 500px;
      margin: 0 auto;

      .menu-cont {
        width: 100%;
        height: 100%;
        background: #fff;
        border-radius: 5px;
        position: relative;
        border: 1px solid #ddd;

        .menu-list {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 50px;
          border-top: 1px solid #ddd;
          box-sizing: border-box;
          background: url("../../../../assets/images/key-board.png") no-repeat 0 0;
          border-radius: 5px;
          padding-left: 43px;

          .menu {
            text-align: center;
            float: left;
            position: relative;
            border-left: 1px solid #44b549;
            color: #44b549;
            line-height: 50px;
            font-size: 14px;
            min-width: 33%;

            .nav-title {
              line-height: 30px;
              display: inline-block;
              width: 100%;
            }

            .sub-menu-list {
              width: 100%;
              position: absolute;
              top: -1px;
              left: 0;
              text-align: center;
              border: 1px solid #ddd;

              li {
                line-height: 45px;
                cursor: pointer;
                overflow: hidden;
                white-space: nowrap;
                border-bottom: 1px solid #ddd;
                padding: 0 5px;
              }

              li:last-of-type {
                border-bottom: 0;
                font-size: 20px;
              }
              .sanjiao {
                position: absolute;
                bottom: -5px;
                display: inline-block;
                width: 0;
                height: 0;
                border-width: 6px;
                border-style: dashed;
                border-color: transparent;
                border-bottom-width: 0;
                border-top-color: #fff;
                border-top-style: solid;
                margin-left: -6px;
                left: 50%;
                &.sanjiao-out {
                  bottom: -6px;
                  border-top-color: #ddd;
                }
              }
            }
          }

          .menu:last-child {
            border-right: 0;
          }
        }
      }
    }
  }
}
.menu-modal-wrapper {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, .7);
  .menu-modal {
    width: 80%;
    background: #fff;
    height: 90%;
    margin: 2.5% auto 0;
    padding-top: 30px;
    .menu-transfer {
      margin: 0 100px;
      display: inline-block;
      text-align: center;
      .ant-transfer-list {
        height: 400px;
        text-align: left;
      }
    }
  }
}