.search-btn {
  width: 240px;
}

.reset-code {
  margin-left: 20px;
  span {
    color: rgb(255, 153, 0);
  }
}

.table-wrapper {
  margin-top: 20px;
  width: 100%;
  * {
    color: #333;
  }
  .ant-tabs-bar {
    margin: 0;
  }
  .gonghao-table {

    * {
      vertical-align: middle;
    }
    .ant-table-thead {
      tr {
        background: rgb(235, 236, 240) !important;
      }
    }
    .ant-table-small > .ant-table-content > .ant-table-body {
      margin: 0;
    }
    .action-wrapper {
      min-width: 240px;
      * {
        color: #fff;
      }
      button {
        margin-right: 10px;
      }
      button:last-child {
        margin-right: 0;
      }
    }
  }
}
