.bread-wrapper {
  margin-bottom:20px;
  * {
    user-select: none;
  }
  .bread-nav {
    width: 100%;
    padding: 10px 20px;
    box-sizing: border-box;
    background: #fff;
    color: #0b0b0b;
    border-radius: 8px;
    .ant-breadcrumb span {
      color: rgb(51, 51, 51);
      font-size: 12px;
    }
  }
}